import React, { useEffect } from 'react';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
function Destinations() {
  useEffect(() => {
    let current = 0;
    const slides = document.querySelectorAll('.destination-card');
    const count = slides.length;

    function next() {
      slides[current].style.display = 'none';
      current = (current + 1) % count;
      slides[current].style.display = 'block';
    }
    
    setInterval(next, 5000);

    // Cleanup function to clear interval when component unmounts
    return () => clearInterval();
  }, []);
  let current = 0;
  let slides;

  useEffect(() => {
    slides = document.querySelectorAll('.destination-card');
  }, []);

  function next() {
    slides[current].style.display = 'none';
    current = (current + 1) % slides.length;
    slides[current].style.display = 'block';
  }

  function prev() {
    slides[current].style.display = 'none';
    current = (current - 1 + slides.length) % slides.length; // Ensure positive index
    slides[current].style.display = 'block';
  }

  return (
    
    <section id="destinations"><h2 id="popular">POPULAR PACKAGES</h2><br /><br />
      <div id="seperate">
      <br />
      <div className="destination-list">
        <div
          className="destination-card"
          id="one"
          onMouseEnter={() => {
            document.getElementById('h1').style.opacity = '0';
          }}
          onMouseLeave={() => {
            document.getElementById('h1').style.opacity = '1';
          }}
        >
          <h2 id="h1">Hawaii</h2>
          <nav>

            <ul className="list2">
              <li className="listele2">
                <a href="#cl">Duration: 7 days</a>
              </li>
              <br />
              <br />
              <li className="listele2">
                <a href="#cl">Price: $500/packs</a>
              </li>
              <br />
              <br />
              <li className="listele2">
                <a href="#cl">Incl. Flight ticket </a>
              </li>
              <br />
              <br />
              <li className="listele2">
                <a href="#cl">Incl. Visa process </a>
              </li>
              <br />
              <br />
            </ul>
          </nav>
          <h5 className="list2" id="description">
            <u>Explore wild and untouched places</u>
          </h5>
          <h5 className="list2" id="details">
            Click here for more details
          </h5>
        </div>
        <div
          className="destination-card"
          id="two"
          onMouseEnter={() => {
            document.getElementById('h3').style.opacity = '0';
          }}
          onMouseLeave={() => {
            document.getElementById('h3').style.opacity = '1';
          }}
        >
          <h2 id="h3">Paris</h2>
          
          <nav>
            <ul className="list2">
              <li className="listele2">
                <a href="#cl">Duration: 7 days</a>
              </li>
              <br />
              <br />
              <li className="listele2">
                <a href="#cl">Price: $500/packs</a>
              </li>
              <br />
              <br />
              <li className="listele2">
                <a href="#cl">Incl. Flight ticket </a>
              </li>
              <br />
              <br />
              <li className="listele2">
                <a href="#cl">Incl. Visa process </a>
              </li>
              <br />
              <br />
            </ul>
          </nav>
          <h5 className="list2" id="description">
            <u>Explore wild and untouched places</u>
          </h5>
          <h5 className="list2" id="details">
            Click here for more details
          </h5>
        </div>
        <div
          className="destination-card"
          id="three"
          onMouseEnter={() => {
            document.getElementById('h5').style.opacity = '0';
          }}
          onMouseLeave={() => {
            document.getElementById('h5').style.opacity = '1';
          }}
        >
          <h2 id="h5">Thailand</h2>
          <nav>
            
            <ul className="list2">
              <li className="listele2">
                <a href="#cl">Duration: 7 days</a>
              </li>
              <br />
              <br />
              <li className="listele2">
                <a href="#cl">Price: $500/packs</a>
              </li>
              <br />
              <br />
              <li className="listele2">
                <a href="#cl">Incl. Flight ticket </a>
              </li>
              <br />
              <br />
              <li className="listele2">
                <a href="#cl">Incl. Visa process </a>
              </li>
              <br />
              <br />
            </ul>
          </nav>
          <h5 className="list2" id="description">
            <u>Explore wild and untouched places</u>
          </h5>
          <h5 className="list2" id="details">
            Click here for more details
          </h5>
        </div>
        <div
          className="destination-card"
          id="four"
          onMouseEnter={() => {
            document.getElementById('h7').style.opacity = '0';
          }}
          onMouseLeave={() => {
            document.getElementById('h7').style.opacity = '1';
          }}
        >
          <h2 id="h7">Ladakh</h2>
          <nav>
            <ul className="list2">
              <li className="listele2">
                <a href="#cl">Duration: 7 days</a>
              </li>
              <br />
              <br />
              <li className="listele2">
                <a href="#cl">Price: $500/packs</a>
              </li>
              <br />
              <br />
              <li className="listele2">
                <a href="#cl">Incl. Flight ticket </a>
              </li>
              <br />
              <br />
              <li className="listele2">
                <a href="#cl">Incl. Visa process </a>
              </li>
              <br />
              <br />
            </ul>
          </nav>
          <h5 className="list2" id="description">
            <u>Explore wild and untouched places</u>
          </h5>
          <h5 className="list2" id="details">
            Click here for more details
          </h5>
        </div>

        
      <button className="prev" onClick={prev}>&#60;</button>
        <button className='next' onClick={next}>&#62;</button>
      </div>
      <div className="options">
        <h3 id="book">Book Tickets</h3>
        <br />
        <FontAwesomeIcon icon="fa-duotone fa-plane-departure" />
        <div className='fromto'>
          <label for="From" id="From-label">From</label>
            <input type="text" list="areas" id='From'
                        placeholder="Departure" />
              <datalist id="areas">
                <option value="Las Vegas">Las Vegas</option>
                <option value="Paris">Paris</option>
                <option value="Hawaii">Hawaii</option>
                <option value="Thailand">Thailand</option>
                <option value="Singapore">Singapore</option>
                <option value="Malaysia">Malaysia</option>
                <option value="New Dehli">New Dehli</option>
                <option value="Chennai">Chennai</option>
                <option value="Kolalambur">Kolalambur</option>
                <option value="Tokyo">Tokyo</option>
                <option value="Seoul">Seoul</option>
                <option value="Dubai">Dubai</option>
                <option value="California">California</option>
              </datalist>
                <br />
                <br />
            <label for="To" id="To-label">To</label>
              <input type="text" list="areas" id="To"
                        placeholder="Arrival" />
                <datalist id="areas">
                  <option value="Las Vegas">Las Vegas</option>
                  <option value="Paris">Paris</option>
                  <option value="Hawaii">Hawaii</option>
                  <option value="Thailand">Thailand</option>
                  <option value="Singapore">Singapore</option>
                  <option value="Malaysia">Malaysia</option>
                  <option value="New Dehli">New Dehli</option>
                  <option value="Chennai">Chennai</option>
                  <option value="Kolalambur">Kolalambur</option>
                  <option value="Tokyo">Tokyo</option>
                  <option value="Seoul">Seoul</option>
                  <option value="Dubai">Dubai</option>
                  <option value="California">California</option>
                </datalist>
          </div>
          <div className='calendar'>
        <label for="From Date" id="from-date-label">From Date  </label>
              <input type="date"  id='from-date' min={new Date().toISOString().split('T')[0]}
                        placeholder="From" />
                        <br />
                        <br />
                        <br />
        <label for="To Date" id="to-date-label">To Date  </label>
              <input type="date"  id='to-date' min={new Date().toISOString().split('T')[0]}
                        placeholder="To" />
       </div>
       <button type='submit' id='submit'>Search Flights</button>
       </div>
      </div>
      
    </section>
  );
}

export default Destinations;
