import * as  React from "react";
import Rating from '@mui/material/Rating';
import Typography from '@mui/material/Typography';
import { useState } from "react"
import Heart from "react-heart"

function Hotels(){
    const [active, setActive] = useState(false);
    const [active1, setActive1] = useState(false);

    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css"></link>
    return (
        <section id="hotel-sec">
            <h1 id="title">HOTELS</h1>
            <div className="hotel-list">
                <div className="hotel-card" id="hotel-one"
                onMouseEnter={()=>{
                    document.getElementById("hotel-one-ul").style.display="block";
                    document.querySelector('#hotel-one .Details').style.display="none";
                }}
                onMouseLeave={()=>{
                    document.getElementById("hotel-one-ul").style.display="none";
                    document.querySelector('#hotel-one .Details').style.display="block";
                }}
                >
                    <div style={{ width: "1.5rem" }}>
			            <Heart isActive={active} onClick={() => setActive(!active)}/>
		            </div>
                    <ul className="hotel-ul" id="hotel-one-ul">
                        <li className="hotel-li">
                        <Typography component="legend">Rating: 4.7/5</Typography>
                        <Rating name="read-only" value={4.7} precision={0.1}readOnly />
                        <br />
                        </li>
                        <li className="hotel-li">Complimentary Breakfast available</li>
                        <li className="hotel-li">Discover the art of relaxation</li>
                    </ul>
                <div className="Details">
                    <h4 className="price">&#x20B9;12,000</h4>
                        <h3 className="hotel-name">Goa</h3>
                    </div>
                </div>

                <div className="hotel-card" id="hotel-two"
                onMouseEnter={()=>{
                    document.getElementById("hotel-two-ul").style.display="block";
                    document.querySelector('#hotel-two .Details').style.display="none";
                }}
                onMouseLeave={()=>{
                    document.getElementById("hotel-two-ul").style.display="none";
                    document.querySelector('#hotel-two .Details').style.display="block";
                }}
                >   
                    <div style={{ width: "1.5rem" }}>
			            <Heart isActive={active1} onClick={() => setActive1(!active1)}/>
		            </div>
                     <ul className="hotel-ul" id="hotel-two-ul">
                        <li className="hotel-li">
                        <Typography component="legend">Rating: 4.3/5</Typography>
                        <Rating name="read-only" value={4.3} precision={0.1} readOnly />
                        <br />
                        </li>
                        <li className="hotel-li">Complimentary Breakfast available</li>
                        <li className="hotel-li">Luxury redefined, service unparalleled</li>
                    </ul>
                    <div className="Details">
                        <h4 className="price">&#x20B9;9,700</h4>
                        <h3 className="hotel-name">Pondicerry </h3>
                    </div>
                </div>

                <div className="hotel-card" id="hotel-three"
                onMouseEnter={()=>{
                    document.getElementById("hotel-three-ul").style.display="block";
                    document.querySelector('#hotel-three .Details').style.display="none";
                }}
                onMouseLeave={()=>{
                    document.getElementById("hotel-three-ul").style.display="none";
                    document.querySelector('#hotel-three .Details').style.display="block";
                }}>
                <ul className="hotel-ul" id="hotel-three-ul">
                        <li className="hotel-li">
                        <Typography component="legend">Rating: 3.75/5</Typography>
                        <Rating name="read-only" value={3.75} precision={0.05} readOnly />
                        <br />
                        </li>
                        <li className="hotel-li">Complimentary Breakfast available</li>
                        <li className="hotel-li">Experience the ultimate in hospitality</li>
                    </ul>
                    <div className="Details">
                        <h4 className="price">&#x20B9;13,500</h4>
                        <h3 className="hotel-name"> Cochin</h3>
                    </div>
                </div>

                <div className="hotel-card" id="hotel-four"
                
                onMouseEnter={()=>{
                    document.getElementById("hotel-four-ul").style.display="block";
                    document.querySelector('#hotel-four .Details').style.display="none";
                }}
                onMouseLeave={()=>{
                    document.getElementById("hotel-four-ul").style.display="none";
                    document.querySelector('#hotel-four .Details').style.display="block";
                }}>
                <ul className="hotel-ul" id="hotel-four-ul">
                        <li className="hotel-li">
                        <Typography component="legend">Rating: 4.5/5 </Typography>
                        <Rating name="read-only" value={4.5} precision={0.5} readOnly />
                        <br />
                        </li>
                        <li className="hotel-li">Complimentary Breakfast available</li>
                        <li className="hotel-li">Luxury redefined, service unparalleled</li>
                    </ul>
                    <div className="Details">
                        <h4 className="price">&#x20B9;16,500</h4>
                        <h3 className="hotel-name">Mysore</h3>
                    </div>
                </div>

                <div className="hotel-card" id="hotel-five"
                
                onMouseEnter={()=>{
                    document.getElementById("hotel-five-ul").style.display="block";
                    document.querySelector('#hotel-five .Details').style.display="none";
                }}
                onMouseLeave={()=>{
                    document.getElementById("hotel-five-ul").style.display="none";
                    document.querySelector('#hotel-five .Details').style.display="block";
                }}>
                <ul className="hotel-ul" id="hotel-five-ul">
                        <li className="hotel-li">
                        <Typography component="legend">Rating: 4.5/5 </Typography>
                        <Rating name="read-only" value={4.5} precision={0.5} readOnly />
                        <br />
                        </li>
                        <li className="hotel-li">Complimentary Breakfast available</li>
                        <li className="hotel-li">Luxury redefined, service unparalleled</li>
                    </ul>
                    <div className="Details">
                        <h3 className="hotel-name">Hotel in Goa</h3>
                    </div>
                </div>
            </div>
        </section>
    );
}
export default Hotels;